html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Raleway", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1299px;
  margin: 0 auto;
  padding: 0 40px;
}

.header {
  padding: 11px 0 15px;
  color: #7d7d7d;
  background-color: #131111;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 40px;
}

.header__translate {
  margin-right: auto;
}

.header__translate-link {
  padding: 0;
  color: #7d7d7d;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: color 0.2s ease;
}
.header__translate-link--current {
  color: #d8d8d8;
  cursor: default;
}
@media (hover: hover) {
  .header__translate-link:hover:not(.focus-visible) {
    color: #d8d8d8;
  }
}
.header__translate-link.focus-visible:focus {
  color: #d8d8d8;
}

.header__site-nav {
  margin-left: 65px;
  max-width: 550px;
}

.header__user-nav {
  margin-left: auto;
  max-width: 152px;
}

.header__shopping-cart {
  margin-left: 26px;
}
.header__shopping-cart svg {
  stroke: #f5f5f5;
  transition: all 0.2s ease;
}
@media (hover: hover) {
  .header__shopping-cart svg:hover:not(.focus-visible) {
    stroke: #000000;
    fill: #f5f5f5;
  }
}
.header__shopping-cart svg.focus-visible:focus {
  stroke: #000000;
  fill: #f5f5f5;
}

.site-nav__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 12px 24px;
  list-style: none;
}

.site-nav__item a {
  color: #7d7d7d;
  transition: color 0.2s ease;
}

.site-nav__item--current a {
  color: #d8d8d8;
  cursor: default;
}
@media (hover: hover) {
  .site-nav__item:hover:not(.focus-visible) a {
    color: #d8d8d8;
  }
}
.site-nav__item.focus-visible:focus a {
  color: #d8d8d8;
}

.user-nav__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  list-style: none;
}

.user-nav__item a {
  width: 26px;
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.user-nav__item a svg {
  stroke: #bcbcbc;
  transition: stroke 0.2s ease;
}
@media (hover: hover) {
  .user-nav__item a:hover:not(.focus-visible) svg {
    stroke: #d8d8d8;
    filter: drop-shadow(0 0 10px #d8d8d8);
  }
}
.user-nav__item a.focus-visible:focus svg {
  stroke: #d8d8d8;
  filter: drop-shadow(0 0 10px #d8d8d8);
}

