.hero-card a.focus-visible:focus {
  color: #ffffff;
  background-color: #f64f00;
}

.hero-card__button text {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 500;
