    background-color: rgba(245, 245, 245, 0.5);
  }
}
.swiper .swiper-button-prev.focus-visible:focus,
.swiper .swiper-button-next.focus-visible:focus {
  color: #000000;
  background-color: rgba(245, 245, 245, 0.5);
}

.swiper .swiper-button-prev {
  border-radius: 30px 0 0 30px;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  font-size: 20px;
}

.swiper-wrapper {
  align-items: stretch;
}

.swiper .swiper-slide {
  height: auto;
  display: flex;
  justify-content: center;
  transform: scale(0.8);
  transition: all 0.7s ease;
}

.swiper-slide .swiper__catalog-link,
.swiper-slide .hero-card__content {
  display: none;
}

.swiper .swiper-slide-active {
  transform: scale(1);
}

.swiper-slide-active .swiper__catalog-link {
  display: flex;
}

.swiper-slide-active .hero-card__content {
  display: block;
}

.footer {
