  color: #333333;
  background-color: #f5f5f5;
}

.footer ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.footer__container {
  display: grid;
  grid-template-columns: 354px auto;
  column-gap: 20px;
  position: relative;
}

.footer__user-nav {
  margin-bottom: 49px;
  grid-column: 1/-1;
  display: flex;
  align-items: center;
}

.footer__logo {
  margin-right: 50px;
}

.footer__sicials-list {
